import { Component, OnInit } from '@angular/core';
import { PyequionStoreService } from '../pyequion-store.service';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

@Component({
  selector: 'app-solution-results-step',
  templateUrl: './solution-results-step.component.html',
  styleUrls: ['./solution-results-step.component.scss']
})
export class SolutionResultsStepComponent implements OnInit {
  objectKeys = Object.keys

  speciesDisplay$: Observable<any[]> = this.pyequionStore.solutionResult$.pipe(
    map(solRst => {
      if (!solRst)
        return null
      const collection = solRst.specie_names.map((v,i) => {
        return {
          name: v,
          conc: solRst.c_molal[i]
        }
      }).filter(val => val.name !== 'H2O' && (!val.name.includes('g')))
      return collection
    })
  )

  precipitatedDisplay$: Observable<any[]> = this.pyequionStore.solutionResult$.pipe(
    map(solRst => {
      if (!solRst)
        return null
      const greaterZero = Object.keys(solRst.preciptation_conc).filter(key => {
        return solRst.preciptation_conc[key] > 1e-20
      })
      return greaterZero
    }),
    filter(names => {
      if (!names)
        return null
      return(!!names.length)
    }
  ))

  constructor(
    private pyequionStore: PyequionStoreService,
  ) { }

  ngOnInit() {
  }

}
