import { Component, OnInit, Input } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';
import compoundsInitial from '../../data/compounds-input.json' //'../data/compounds-input.json'
import { ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-autocomplete-compound',
  templateUrl: './autocomplete-compound.component.html',
  styleUrls: ['./autocomplete-compound.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AutocompleteCompoundComponent implements OnInit {
  @Input() control: FormControl
  // myControl = new FormControl();
  options: string[] = compoundsInitial.compounds
  filteredOptions: Observable<string[]>;

  ngOnInit() {
    this.filteredOptions = this.control.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().includes(filterValue));
  }
}
