import { Injectable } from '@angular/core';
import { PyequionService } from './pyequion.service';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';



@Injectable({
  providedIn: 'root'
})
export class PyequionStoreService {

  private eqSystemSubject = new BehaviorSubject<EquilibriumModel>(null);
  public eqSystem$ = this.eqSystemSubject.asObservable();
  public isLoading: boolean = false

  private solutionResultSubject = new BehaviorSubject<any>(null);
  public solutionResult$ = this.solutionResultSubject.asObservable();

  readonly reactionsLatex$ = this.eqSystem$.pipe(
    map(eqSystem =>{
      if (!eqSystem)
        return null
      const convertedReacs = eqSystem.reactionsLatex.map(this.fixReactionLatex())
      return convertedReacs
      })
  )

  readonly reactionsLatexAsConcatenatedString$ = this.eqSystem$.pipe(
    map(eqSystem =>{
      if (!eqSystem)
        return null
      const convertedReacs = eqSystem.reactionsLatex.map(this.fixReactionLatex())
      // return convertedReacs
      // return '$a^2$'
      return {latex: '$' + convertedReacs.join('\\\\ ') + '$'}
      })
  )

  readonly solidReactionsLatex$ = this.eqSystem$.pipe(
    map(eqSystem =>{
      if (!eqSystem)
        return null
      const convertedReacs = eqSystem.solidReactionsLatex.map(this.fixReactionLatex())
      if (convertedReacs.length == 1 && convertedReacs[0] == "\\ce <=}")
        return null //some kind of bug... (fix backend?)
      console.log(convertedReacs);
      return convertedReacs
      })
  )

  readonly solidReactionsLatexAsConcatenatedString$ = this.eqSystem$.pipe(
    map(eqSystem =>{
      if (!eqSystem)
        return null
      const convertedReacs = eqSystem.solidReactionsLatex.map(this.fixReactionLatex())
      if (convertedReacs.length == 1 && convertedReacs[0] == "\\ce <=}")
        return null //some kind of bug... (fix backend?)
      // console.log(convertedReacs);
      // return convertedReacs
      return {latex: '$' + convertedReacs.join('\\\\ ') + '$'}
      })
  )

  constructor(private pyequionService: PyequionService) {
  }

  private fixReactionLatex(): (value: any, index: number, array: any[]) => any {
    return reac => {
      if (reac.includes('irrev')) {
        const aux1 = 'a';
      }
      else if (reac.includes('__')) {
        const aux1 = reac.split('__');
        const aux2 = aux1[1].split('^');
        const solidName = aux2[0];
        const newStr = reac.replace('__' + solidName, `_\{${solidName}\}`);
        return newStr;
      }
      else {
        return reac;
      }
    };
  }

  async createEquilibrium(inputCompounds: InputCompounds) {
    this.isLoading = true
    const respRPC = await this.pyequionService.createEquilibrium(inputCompounds).toPromise()
    this.isLoading = false
    const eqSystem: EquilibriumModel = respRPC.result
    this.eqSystemSubject.next(eqSystem)
  }

  async calculateEquilibrium(inputEqCalculation: InputCalculationModel) {
    this.isLoading = true
    const respRPC = await this.pyequionService.calculateEquilibrium(inputEqCalculation).toPromise()
    const solutionResult = respRPC.result
    this.isLoading = false
    this.solutionResultSubject.next(solutionResult)
  }

  emitValueEqSystem(val: any) {
    this.eqSystemSubject.next(val)
  }

  getValueEqSystem() {
    return this.eqSystemSubject.getValue()
  }

  emitValueSolutionResults(val: any) {
    this.solutionResultSubject.next(val)
  }

  // auxPrintReaction(reaction: any) {
  //   const arrStr = Object.keys(reaction).map((key:string) => {
  //     return `${reaction[key]}${key}`
  //   })
  //   const reactStr = arrStr.join(';')
  //   return reactStr
  // }

  // async fetchAll() {
  //   this.todos = await this.todosService.index().toPromise();
  // }
}
