import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormBuilder, Validators } from '@angular/forms';
import { mapCloseEquationEnum } from '../constants';

@Component({
  selector: 'app-species-input-step',
  templateUrl: './species-input-step.component.html',
  styleUrls: ['./species-input-step.component.scss']
})
export class SpeciesInputStepComponent implements OnInit {
  @Input() compoundsInputForm
  @Input() equilibriumCalcForm
  @Output() onClickCreateEquilibrium = new EventEmitter<any>()

  closingEquationTypes = [
    {label: 'Open - CO2(g)', value: 0},
    {label: 'Total Inorganic Carbon', value: 1},
    {label: 'pH (to do)', value: 2},
    {label: 'None', value: mapCloseEquationEnum['NONE']},
  ]

  constructor(
    private fb: FormBuilder,
  ) { }

  ngOnInit() {
  }

  handleMoreInputCompound() {
    this.compoundsInput.push(this.fb.control('', Validators.required));
    this.concentrations.push(this.fb.control(1.0, Validators.required));
  }

  handleRemoveCompound(i: number) {
    if (!this.compoundsInput.length)
      return
    if (this.compoundsInput.length == 1) {
      this.compoundsInput.controls[i].setValue('')
      return
    }
    if ((i+1) === this.compoundsInput.length) {
      this.compoundsInput.controls[i].setValue('')
    }
    this.compoundsInput.controls.splice(i, 1)
    this.concentrations.controls.splice(i, 1)
  }

  get compoundsInput() {
    return this.compoundsInputForm.get('compounds') as FormArray;
  }

  get concentrations() {
    return this.equilibriumCalcForm.get('concentrations') as FormArray;
  }

  get closingEqType() {
    return this.compoundsInputForm.get('closingEqType');
  }

}
