import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of, BehaviorSubject } from 'rxjs';
import { environment } from "../environments/environment";
import { tap, catchError, map, debounceTime, distinctUntilChanged, switchMap, exhaust, exhaustMap } from 'rxjs/operators';

const URL = environment.urlBackend
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
@Injectable()
export class PyequionService {

  // private reactionsSubject = new BehaviorSubject<any>(null);
  // public reactions$ = this.reactionsSubject.asObservable();

  constructor(
    private http: HttpClient,
  ) {
    // // Test only
    // this.createEquilibrium().subscribe(resp => {
    //   console.log(resp)
    // })
  }

  // public emitSimDataBeforeRun(v: any) {
  //   this.reactionsSubject.next(v);
  // }

  startUp(): Observable<any> {
    const body = {
      "jsonrpc": "2.0",
      "method": "App.startup",
      "params": {},
      "id": "3"
    }
    return this.http.post(URL, body, httpOptions).pipe(
      tap((payload) => this.log(`startup`)),
      distinctUntilChanged(),
      exhaustMap(resp => of(resp)), //(resp => of(resp)),
      map((resp: any) => {
        console.log('HIT MAP in Startup')
        if (!resp) {
          throw new Error('Request for pyequion backend failed');
        }
        if ('error' in resp) {
          throw new Error(resp.error.message);
        }
        return resp
      }),
      catchError(this.handleError('startUp'))
    );
  }

  createEquilibrium(inputCompounds: InputCompounds): Observable<JSONRPCModel> {
    const body = {
      "jsonrpc": "2.0",
      "method": "App.create_equilibrium",
      "params": inputCompounds,
      "id": "1"
    }
    return this.http.post(URL, body, httpOptions).pipe(
      tap((newHero) => this.log(`posted`)),
      // debounceTime(1000),
      // distinctUntilChanged(), //RETURN ME AFTER CHECKING WITH ASYNC SERVER (G CLOUD)
      // exhaustMap(resp => of(resp)), //(resp => of(resp)),
      map((resp: any) => {
        console.log('HIT MAP in Post CREATE')
        if (!resp) {
          throw new Error('Request for pyequion backend failed');
        }
        if ('error' in resp) {
          throw new Error(resp.error.message);
        }
        return resp
      }),
      catchError(this.handleError('createEq'))
    );
  }

  calculateEquilibrium(inputEqCalculation: InputCalculationModel): Observable<JSONRPCModel> {
    const body = {
      "jsonrpc": "2.0",
      "method": "App.solve_equilibrium",
      "params": inputEqCalculation,
      "id": "1"
    }
    return this.http.post(URL, body, httpOptions).pipe(
      tap((newHero) => this.log(`posted`)),
      // debounceTime(1000),
      distinctUntilChanged(),
      exhaustMap(resp => of(resp)), //(resp => of(resp)),
      map((resp: any) => {
        console.log('HIT MAP in Post SOLVE')
        if (!resp) {
          throw new Error('Request for pyequion backend failed');
        }
        if ('error' in resp) {
          throw new Error(resp.error.message);
        }
        return resp
      }),
      catchError(this.handleError('createEq'))
    );
  }

  // return this.http.post<JSONRPCModel>(URL, body, httpOptions).pipe(
  //   tap((newHero: JSONRPCModel) => this.log(`posted`)),
  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  log(msg: string) {
    console.log(msg)
  }
}
